.subscribe__wrapper {
  min-height: 100vh;
  color: #000;
  display: -ms-grid;
  background-color: #b9b7b7;
  align-items: center;
  justify-items: center;
  font-family: Muli, sans-serif;
  font-size: 1rem;
  display: grid;
}

.subscribe__wrapper.is-hidden, .subscribe__btn.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.subscribe__container {
  max-width: 768px;
  display: -ms-grid;
  -webkit-box-shadow: 0px 17px 34px -20px var(--main-accent-color);
  box-shadow: 0px 17px 34px -20px var(--main-accent-color);
  max-width: 400px;
  background-color: #fff;
  border-radius: 1em;
  align-items: center;
  justify-items: center;
  padding: 0;
  display: grid;
  overflow: hidden;
}

.subscribe__img {
  height: 200px;
  position: relative;
  overflow: hidden;
}

.subscribe__img-icon {
  z-index: 1;
  width: 100%;
  height: auto;
  z-index: 2;
  -o-object-fit: cover;
  object-fit: cover;
  border-radius: 1em 1em 0 0;
  transform: translateY(-50%);
}

.subscribe__content {
  display: -ms-grid;
  align-items: center;
  justify-items: center;
  margin-bottom: 1.2em;
  padding: 0 2em;
  display: grid;
}

.subscribe__subtitle {
  text-transform: uppercase;
  margin-top: 2.3em;
  margin-bottom: .9em;
  font-size: .9rem;
  font-weight: 700;
}

.subscribe__title {
  margin-bottom: .9em;
  font-size: 1.4rem;
  font-weight: 700;
}

.subscribe__input {
  width: 100%;
  border: 2px solid var(--main-accent-color);
  border-radius: .5em;
  margin-bottom: 1em;
  padding: .5em 0 .5em .5em;
  font-size: .8em;
  line-height: 3;
}

::-webkit-input-placeholder {
  color: #656565;
}

:-ms-input-placeholder {
  color: #656565;
}

::-ms-input-placeholder {
  color: #656565;
}

::placeholder {
  color: #656565;
}

.subscribe__btn {
  color: #fff;
  background-color: var(--main-accent-color);
  width: 100%;
  cursor: pointer;
  text-transform: uppercase;
  border: none;
  border-radius: .5em;
  margin-bottom: 40px;
  padding: .9em 0;
  font-size: 1.3rem;
  font-weight: 700;
  display: inline-block;
}

.subscribe__btn:hover, .subscribe__btn:focus {
  background-color: #de6f1a;
}

.subscribe__span {
  color: #50ac59;
  margin-bottom: 10px;
  display: block;
}

@keyframes pulse {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  33% {
    opacity: 1;
    transform: scale(1);
  }

  100% {
    opacity: 0;
    transform: scale(3);
  }
}

.subscribe__close {
  color: #5d5a5a;
  height: 42px;
  text-transform: uppercase;
  cursor: pointer;
  z-index: 15;
  background: none;
  border: none;
  outline: none;
  padding: 4px 10px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  transition: all .3s;
  position: absolute;
  top: 10px;
  right: 10px;
}

.subscribe__close:hover, .subscribe__close:focus {
  color: orange;
}

